// TiktokViewer.js
import Hls from "hls.js";
import React, { useEffect, useRef, useState } from "react";
import Loader from "./Loader";

const TiktokViewer = () => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [soundOn, setSoundOn] = useState(false);
  const videoRefs = useRef([]);

 useEffect(() => {
  fetch("https://offermeqa.com/data/products_data.json")
    .then((res) => res.json())
    .then((data) => {
      const videos = data.filter((item) => item.type === "video");

      Promise.all(
        videos.map((item) =>
          fetch(`/uploads/settings_user_${encodeURIComponent(item.user_id)}.json`)
            .then((res) => res.json())
            .then((settings) => {
              console.log("✅ شعار الزبون:", item.user_id, settings.logo);
              return {
                ...item,
             userImage: settings.logo
  ? `/uploads/avatars/logos/${encodeURIComponent(item.user_id)}_logo.jpg`
  : "/static/img/user.png",


                whatsapp: settings.whatsapp || "",
                affiliate: item.affiliate || "",
              };
            })
            .catch(() => {
              console.warn("⚠️ لم يتم العثور على إعدادات الزبون:", item.user_id);
              return {
                ...item,
                userImage: "/static/img/user.png", // ✅ صورة افتراضية عند الخطأ
              };
            })
        )
      ).then((updated) => {
        setProducts(updated);
        setLoading(false);
      });
    })
    .catch((err) => {
      console.error("فشل تحميل المنتجات:", err);
      setLoading(false);
    });
}, []);



  useEffect(() => {
  const observer = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry) => {
        const video = entry.target.querySelector("video");
        if (!video) return;

        if (entry.isIntersecting) {
          video.muted = !soundOn;
          video.play().catch(() => {});
        } else {
          video.pause();
          video.muted = true; // ✅ منع الصوت من الفيديوهات الغير ظاهرة
        }
      });
    },
    { threshold: 0.8 }
  );

  videoRefs.current.forEach((ref) => {
    if (ref) observer.observe(ref);
  });

  return () => observer.disconnect();
}, [products, soundOn]);



  const toggleSave = (index, url) => {
  let saved = JSON.parse(localStorage.getItem("savedProducts") || "[]");

  if (saved.includes(url)) {
    saved = saved.filter((item) => item !== url);
  } else {
    saved.push(url);
  }

  localStorage.setItem("savedProducts", JSON.stringify(saved));
  setProducts((prev) => [...prev]); // لإعادة الريندر
};

  const toggleLike = (index) => {
  

    const btn = document.getElementById(`like-${index}`);
    if (!btn) return;

    const liked = btn.dataset.liked === "true";
    btn.querySelector("img").src = liked ? "/static/img/like.png" : "/static/img/liked.png";

    btn.dataset.liked = liked ? "false" : "true";
  };

  return (
    <div style={containerStyle}>
      <button onClick={() => setSoundOn(!soundOn)} style={soundBtnStyle}>
        {soundOn ? "🔊 صوت" : "🔇 كتم"}
      </button>

      {loading ? (
        <Loader />
      ) : (
        products.map((item, index) => (
          <div
            key={item.id}
            ref={(el) => (videoRefs.current[index] = el)}
            style={slideStyle}
          >
            <video
  ref={(el) => {
    if (el && item.url.endsWith(".m3u8")) {
      if (Hls.isSupported()) {
        const hls = new Hls();
        hls.loadSource(item.url);
        hls.attachMedia(el);
      } else if (el.canPlayType("application/vnd.apple.mpegurl")) {
        el.src = item.url;
      }
    } else if (el) {
      el.src = item.url;
    }
  }}
  muted={!soundOn}
  autoPlay
  loop
  playsInline
  style={videoStyle}
/>

            {/* أزرار الجهة اليمنى */}
          <div style={rightButtonsStyle}>
  {/* صورة الزبون */}
  <img src={item.userImage} alt="user" style={userImageStyle} />

  {/* زر الإعجاب */}
  <button id={`like-${index}`} data-liked="false" onClick={() => toggleLike(index)} style={iconBtn}>
    <img src="/static/img/like.png" alt="Like" style={iconImageStyle} />
  </button>

  {/* زر المتجر */}
  <button onClick={() => window.open(`store.html?user_id=${item.user_id}&visitor=true`, "_blank")} style={iconBtn}>
    <img src="/static/img/store.png" alt="Store" style={iconImageStyle} />
  </button>

  {/* زر المشاركة */}
  <button
    onClick={() =>
      navigator.share
        ? navigator.share({ url: item.url })
        : alert(`انسخ الرابط: ${item.url}`)
    }
    style={iconBtn}
  >
    <img src="/static/img/share.png" alt="Share" style={iconImageStyle} />
  </button>

  {/* زر الحفظ */}
  <button onClick={() => toggleSave(index, item.url)} style={iconBtn}>
    <img
      src={`/static/img/${
        JSON.parse(localStorage.getItem("savedProducts") || "[]").includes(item.url)
          ? "saved.png"
          : "save.png"
      }`}
      alt="Save"
      style={iconImageStyle}
    />
  </button>

  {/* زر الشراء أو واتساب */}
  {item.affiliate && item.affiliate !== "" ? (
  <button onClick={() => window.open(item.affiliate, "_blank")} style={iconBtn}>
    <img src="/static/img/aliexpress.png" alt="AliExpress" style={iconImageStyle} />
  </button>
) : item.whatsapp && item.whatsapp !== "" ? (
  <button onClick={() => window.open("https://wa.me/974" + item.whatsapp, "_blank")} style={iconBtn}>
    <img src="/static/img/whatsapp.png" alt="WhatsApp" style={iconImageStyle} />
  </button>
) : null}

</div>
            {/* نص المنتج أسفل الفيديو */}
            <div style={overlayStyle}>
              <h3>{item.name}</h3>
              <p>{item.description}</p>
            </div>
          </div>
        ))
      )}
    {/* ✅ الفوتر الثابت مثل تيك توك */}
  <div style={footerStyle}>
    <button style={footerBtn} onClick={() => alert("الصفحة الرئيسية")}>
      <img src="/static/img/home.png?v=3" alt="Home" style={footerIcon} />

      <span>الرئيسية</span>
    </button>

    <button style={footerBtn} onClick={() => alert("بحث قادم")}>
      <img src="/static/img/search.png?v=3" alt="Search" style={footerIcon} />
      <span>بحث</span>
    </button>

    <button style={footerBtn} onClick={() => window.location.href = "/upload.html"}>
      <img src="/static/img/plus.png" alt="Upload" style={footerIcon} />
      <span>إضافة</span>
    </button>

    <button style={footerBtn} onClick={() => alert("حسابي")}>
      <img src="/static/img/profile.png" alt="Profile" style={footerIcon} />
      <span>حسابي</span>
    </button>
  </div>
</div>
);
};


const containerStyle = {
  width: "100%",
  height: "100vh",
  overflowY: "scroll",
  scrollSnapType: "y mandatory",
  backgroundColor: "#000"
};

const slideStyle = {
  scrollSnapAlign: "start",
  height: "100vh",
  width: "100vw",
  position: "relative",
  overflow: "hidden",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
};


const videoStyle = {
  display: "block",
  margin: "auto",
  maxWidth: "100%",
  maxHeight: "100%",
  objectFit: "contain",
  backgroundColor: "#000",
};






const overlayStyle = {
  position: "absolute",
  bottom: "220px", // ✅ كان 160، زدناه حتى يكون فوق الفوتر
  left: "20px",
  color: "#fff",
  zIndex: 10,
  paddingRight: "80px"
};


const rightButtonsStyle = {
  position: "absolute",
  right: "10px",
  bottom: "200px",
  display: "flex",
  flexDirection: "column",
  gap: "12px",
  alignItems: "center",
  zIndex: 999
};

const iconBtn = {
  background: "transparent",
  border: "none",
  borderRadius: "50%",
  width: "48px",
  height: "48px",
  cursor: "pointer",
  padding: 0
};


const userImageStyle = {
  width: "54px",
  height: "54px",
  borderRadius: "50%",
  border: "2px solid #fff",
  objectFit: "cover"
};

const soundBtnStyle = {
  position: "fixed", // ✅ لازم يكون fixed وليس absolute
  top: "20px",       // ✅ بدلاً من bottom
  right: "20px",
  zIndex: 10000,
  background: "#000",
  color: "#fff",
  border: "none",
  borderRadius: "10px",
  padding: "8px 14px",
  fontSize: "14px",
  cursor: "pointer",
  boxShadow: "0 0 10px rgba(0,0,0,0.3)"
};


const iconImageStyle = {
  width: "32px",
  height: "32px",
  objectFit: "contain",
  display: "block",
};



const footerStyle = {
  position: "fixed",
  bottom: 0,
  left: 0,
  width: "100%",
  background: "#111",
  display: "flex",
  justifyContent: "space-around",
  alignItems: "center",
  padding: "8px 0",
  zIndex: 1000
};

const footerBtn = {
  background: "none",
  border: "none",
  color: "#fff",
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  fontSize: "12px",
  padding: "4px",
  minWidth: "64px" // ✅ توحيد العرض
};


const footerIcon = {
  width: "28px",
  height: "28px",
  marginBottom: "4px",
  objectFit: "contain"
};


export default TiktokViewer;
